.app {
    font-family: Arial, sans-serif;
    padding: 20px;
  }
  
  .header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .logo {
    height: 40px;
    margin-right: 20px;
  }
  
  .header h1 {
    font-size: 24px;
    margin: 0;
  }
  
  .booking-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .booking-table th, .booking-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .booking-table th {
    background-color: #f4f4f4;
  }
  
  .booking-table .status {
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
  }
  
  .booking-table .status.red {
    background-color: red;
  }
  
  .booking-table .status.green {
    background-color: green;
  }
  